.footer-wrapper{position:relative;padding:80px 0 40px;background:#0c1121}.footer-wrapper::before{content:'';background-image:url(/techdrug/static/media/footer-bg.b9bb4e53.svg);display:block;position:absolute;left:0;right:0;top:-7rem;height:30rem;width:100%;z-index:9}.footer-wrapper .container{position:relative;z-index:9}.footer-wrapper .footer-image img{display:block;margin:0 auto}.footer-wrapper .footer-nav .footer-nav-list{padding:0}.footer-wrapper .footer-nav .footer-nav-list .footer-nav-item{list-style:none}.footer-wrapper .footer-nav .footer-nav-list .footer-nav-item a{font-size:16px;padding:0.4rem 0 0;margin-top:1rem;color:#fff;font-weight:600;line-height:1.5;display:inline-block}.footer-wrapper .footer-subscribe{padding-top:20px}.footer-wrapper .footer-subscribe form input{width:100%;min-height:2.8rem;padding:0 1rem}.footer-wrapper .footer-subscribe form button{border:0;border-radius:.1rem;color:#fff;padding:.5rem 1rem;margin-top:0.8rem;margin-bottom:1rem;float:right;box-shadow:0 0.5rem 0.8rem rgba(50,50,93,0.15),0 0.1rem 0.4rem rgba(0,0,0,0.12);-moz-box-shadow:0 0.5rem 0.8rem rgba(50,50,93,0.15),0 0.1rem 0.4rem rgba(0,0,0,0.12);-webkit-box-shadow:0 0.5rem 0.8rem rgba(50,50,93,0.15),0 0.1rem 0.4rem rgba(0,0,0,0.12);background:#c22136;font-weight:600}.footer-wrapper p{font-size:16px;color:#fff;font-weight:600}.footer-wrapper p a{color:#c22236}

.sliderWrapper{background:linear-gradient(240.67deg, rgba(87,118,219,0.75) 10.22%, rgba(0,37,158,0.75) 87.66%),#5776db;padding:120px 15px 80px}.sliderWrapper .slick-arrow.slick-prev{top:unset;bottom:0;left:0;z-index:99}.sliderWrapper .slick-arrow.slick-prev::before{font-size:35px}.sliderWrapper .slick-arrow.slick-next{top:unset;bottom:0;left:50px;z-index:99}.sliderWrapper .slick-arrow.slick-next::before{font-size:35px}.sliderWrapper .Content-slider{display:flex;flex-direction:column;justify-content:center;height:100%;color:#fff}.sliderWrapper .Content-slider h1{font-size:60px;text-transform:uppercase;font-weight:600}.sliderWrapper .Content-slider p{font-size:18px;line-height:30px;font-weight:400;letter-spacing:1px}.sliderWrapper .slick-dots{background:#41dfc0;max-width:150px;display:flex !important;justify-content:center;border-radius:17px;bottom:5px;padding:10px 15px;bottom:-30px;padding:10px 15px;left:50%;transform:translateX(-50%)}.sliderWrapper .slick-dots li button:before{line-height:normal;line-height:initial;color:transparent}.slick-slide *{outline:0 !important}.slick-dots li button:before,.slick-dots li button,.slick-dots li{height:auto;width:auto}.slick-dots li button{background:#fff;border-radius:9px;width:15px;height:15px}.slick-dots li.slick-active button{opacity:0.7}

.portfolio-row .box{position:relative;padding:15px;box-shadow:0 0.15rem 0.55rem -0.1rem rgba(50,50,93,0.25),0 0.3rem 1rem -2rem rgba(0,0,0,0.3),0 -0.1rem 1rem -0.1rem rgba(0,0,0,0.025);transition:0.2s ease-in-out}.portfolio-row .box:hover{box-shadow:0 -0.45rem 0.85rem 0 rgba(0,0,0,0.15),0 1rem 0.95rem 0.1rem rgba(0,0,0,0.15)}.portfolio-row .box .box-label .label-bg{background-image:url(/techdrug/static/media/label-purple.2e78cfa7.svg);width:195px;height:35px;position:absolute;left:50%;transform:translateX(-50%);top:0}.portfolio-row .box .box-label .label-text{position:absolute;top:0;left:50%;transform:translateX(-50%);font-size:16px;line-height:16px;color:#fff;padding:8px;font-weight:500}.portfolio-row .box .box-thumnails{padding:50px 0 20px}.portfolio-row .box .box-content h3{font-size:20px;font-weight:700;text-align:center}

.testimonial-reel{margin-bottom:4rem !important;margin-top:4rem}.testimonial-reel .box{position:relative;margin-bottom:30px}.testimonial-reel .box .image{margin:0 auto -45px;text-align:center}.testimonial-reel .box .image img{height:130px;margin:0 auto}.testimonial-reel .box .test-component{background-color:#fff;padding:1rem 2.5rem;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15);min-height:200px}.testimonial-reel .box .test-title{font-family:"Open Sans",sans-serif;color:#f96d02;text-align:center;margin-top:3.3rem;margin-bottom:15px}.testimonial-reel .box .test-content{text-align:center}.testimonial-reel .slick-slide{opacity:0.3;transition:opacity 0.3s;padding-left:1rem;padding-right:1rem}.testimonial-reel .slick-slide.slick-cloned{opacity:0.3;transition:opacity 0.3s}.testimonial-reel .slick-slide.slick-current.slick-active{opacity:1;transition:opacity 0.3s}

