tn-warapper .main-btn .slash{background-image:url(/techdrug/static/media/splash.49b6be56.svg);height:20px;width:10px;background-size:100%;display:block;background-repeat:no-repeat;position:absolute;left:20px;top:48%;transform:translateY(-50%)}.btn-warapper .main-btn.white-btn{color:#2642b3;background:#fff}.btn-warapper .main-btn.main-color{color:#fff;background:#c22136}.btn-warapper .main-btn.main-color .slash{background-image:url(/techdrug/static/media/whitesplash.65715b9e.svg)}.btn-warapper .main-btn::after,.btn-warapper .main-btn::before{position:absolute;font-family:FontAwesome;top:50%;transform:translateY(-50%);font-size:14px;font-weight:100}.btn-warapper .main-btn::after{content:'\f054';right:8px}.btn-warapper .main-btn::before{left:8px;content:'\f053'}.btn-warapper.button-bt-wrapper{margin-top:30px}.btn-warapper.button-bt-wrapper .main-btn{padding:20px 30px 20px 43px}.banner-btn{border-style:solid;border-color:#fff;border-width:2px;background:transparent;color:#fff;white-space:nowrap}@media (max-width: 1025px){.banner-btn{position:absolute;right:15px}}@media (max-width: 991px){.banner-btn{position:absolute;right:85px;top:-3px}}.brands .card{box-shadow:0 0.5rem 0.8rem rgba(50,50,93,0.15),0 0.1rem 0.4rem rgba(0,0,0,0.12);border-radius:4px;border:0}.brands .card img{min-height:110px;object-fit:contain;padding:10px;border-bottom:1px solid #ccc}.brands .card .card-body{color:#000}.brands .card .card-body .card-title{font-size:1rem;color:#000;text-transform:capitalize;font-weight:400;letter-spacing:0.5px;margin:0}

